<template>
  <div class="_timer-menu layer-box">
    <button class="_timer-menu-button">타이머 초기화</button>
    <div class="_timer-count-box">
      <countdown
        class="time-count"
        v-if="countdownStatus !== 'end'"
        :time="settedTime * 1000"
        :interval="1000"
        @end="onCountdownEnd"
        @progress="onCountdownProgress"
      >
        <div class="time-count-number" slot-scope="props">
          {{ props.seconds }}
          <span class="time-count-text">초</span>
        </div>
      </countdown>
      <div class="timer-button--wrap">
        <radial-progress-bar
          :diameter="120"
          :total-steps="settedTime"
          :completed-steps="completedSteps"
          :animate-speed="1000"
          :stroke-width="5"
          :startColor="'#10c469'"
          :stopColor="'#5b69bc'"
          :inner-stroke-color="'#232831'"
          :timing-func="'linear'"
        ></radial-progress-bar>
      </div>
    </div>
  </div>
</template>

<script>
import Countdown from "@/components/util/Countdown.vue";
import RadialProgressBar from "@/components/util/RadialProgressBar.vue";

export default {
  name: "TimerMenu",
  components: {
    Countdown,
    RadialProgressBar
  },
  props: {
    settedTime: {
      type: Number,
      default: 40
    }
  },
  data: () => {
    return {
      countdownNumber: 15,
      countdownStatus: "ready"
    };
  },
  computed: {
    completedSteps() {
      return this.settedTime - this.countdownNumber;
    },
    isCountdownReady() {
      return this.countdownStatus === "ready";
    }
  },
  methods: {
    onCountdownProgress(time) {
      this.countdownNumber = time.seconds - 1 > 0 ? time.seconds - 1 : 0;
    },
    onCountdownEnd() {
      this.countdownStatus = "end";
    },
    onClickTimer() {
      if (this.isCountdownReady) {
        this.countdownStatus = "start";
      }
    },
    initTimer() {
      this.countdownStatus = "end";
      this.$nextTick(() => {
        this.countdownNumber = this.settedTime;
        this.countdownStatus = "ready";
      });
    }
  }
};
</script>

