<template>
  <div class="_player-panel" :class="{ _active: player.turnOn, _ended: !player.live }">
    <PlayerInfo :player="player" />
    <ScoreMenu />
    <TurnMenu />
  </div>
</template>

<script>
import PlayerInfo from "@/components/info/PlayerInfo.vue";
import ScoreMenu from "@/components/menu/ScoreMenu.vue";
import TurnMenu from "@/components/menu/TurnMenu.vue";

export default {
  name: "PlayerPanel",
  components: {
    PlayerInfo,
    ScoreMenu,
    TurnMenu
  },
  props: {
    player: {
      type: Object,
      default: null
    }
  },
  data: () => {
    return {};
  },
  computed: {
    currentScore() {
      return this.player.getScore;
    }
  },
  methods: {
    increaseScore(score) {
      this.$emit("score", this.player.name, score);
    }
  }
};
</script>

<style scoped>
._player-panel {
  height: 100%;
  display: flex;
  flex-direction: column;
}

._current-score {
  position: relative;
  font-size: 35pt;
}
</style>
